::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
}

header {
  background-color: #6b7280;
  padding: 15px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 15px;
}

header > h1 {
  font-size: 2.5rem;
  color: #e5e7eb;
}

.search-box {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.search-box > input {
  font-size: 1rem;
  background-color: #e5e7eb;
  padding: 6px;
  border: none;
  border-radius: 5px;
}

.search-box > button {
  font-size: 1rem;
  font-weight: bold;
  color: #1f2937;
  background-color: #e5e7eb;
  padding: 6px 8px;
  border: none;
  border-radius: 5px;
}

main {
  background-color: #e5e7eb;
  flex-grow: 1;
}

.settings-container {
  font-size: 1rem;
  font-weight: bold;
  color: #1f2937;
  padding: 5px 10px;
  display: flex;
  justify-content: end;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.settings-container select {
  font-size: 0.9rem;
  font-weight: bold;
  padding: 2px 5px;
  border-radius: 5px;
  outline: none;
}

.location {
  font-size: 1.8rem;
  color: #1f2937;
  text-align: center;
  margin: 20px 0 30px;
}

.weather-data-container {
  color: #e5e7eb;
  padding: 10px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 100px;
}

.weather-data-container > div > h3 {
  font-size: 1.5rem;
  text-align: center;
}

.weather-data-container ul {
  font-size: 1.2rem;
  font-weight: bold;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.weather-data-container ul > img {
  width: 75px;
  height: auto;
  margin: 0 auto;
}

.current-weather,
.tomorrow-this-hour-weather {
  width: 600px;
  height: auto;
  background-color: #6b7280;
  padding: 10px;
  border-radius: 5px;
}

footer {
  background-color: #6b7280;
  color: #e5e7eb;
  font-weight: bold;
  padding: 10px;
  display: grid;
  place-content: center;
}
